.td-fit {
    white-space: nowrap;
    width: 1px;
}
.navbar {
    margin-bottom: 1rem;
}
.center {
    text-align: center;
}
.right {
    text-align: right;
}
.table-container {
    margin-top: 1rem;
}
.table-title {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    cursor: pointer;
}
.table-title h5 {
    margin: 0;
    -webkit-align-self: center;
            align-self: center;
}
.nav-logout {
    cursor: pointer;
}
.top-right-button {
    padding-top: 1rem;
}

@media only screen and (min-width: 768px){
    .top-right-button {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: flex-end;
                justify-content: flex-end;
    }  
}

.top-right-button button+button {
    margin-left: 1rem;
}

.card{
    margin-bottom: 2rem;
}

.button-row {
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.table th.rolltable-header {
    vertical-align: middle;
}

.rolltable-dice-header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.rolltable-dice-formula {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.inline-buttons {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.td-entry{
    cursor: pointer;
}
.result-text {
    white-space: pre-line;
}
.labelText {
    font-weight: bold;
}
.view-tables {
    margin-top: 2rem;
}
.roll-button {
    margin-right: 1rem;
}

.pages-button-row {
    margin-top: 2rem;
}
.pages-button-row + .pages-button-row {
    margin-top: 0.5rem;
}
